body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.createDiv {
  font-family: Helvetica, sans-serif;
  color: #404040;
  display: flex;
  justify-content: center;
  margin: auto;
}

.createh1 {
  font-size: 30px;
}

.createh3 {
  font-size: 15px;
  padding: 0 0 3px 0;
  margin: 0;
}

.createInput {
  height: 20px;
  width: 400px;
  margin: 0 0 20px 0;
  border: #404040 solid 1.4px;
  border-radius: 3px;
}

.createConservationStatus {
  height: 100px;
  width: 400px;
  border-radius: 3px;
  border: #404040 solid 1.4px;
  margin: 0 0 20px 0;
}
.homeDiv {
  font-family: Helvetica, sans-serif;
}

.homeh2 {
    text-align: left;
    color: #404040;
    font-size: 30px;
    padding: 50px 0 50px 70px;
}

.homeSection {
  display: flex;
  justify-content: space-between;
}

nav {
  text-align: right;
  width: 80%;
  margin: auto;
  font-style: italic;
  font-size: 20px;
}

a {
  text-decoration: none;
  color: #404040;
}

.homeContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: auto;
}
.showDiv {
  font-family: Helvetica, sans-serif;
  display: flex;
  width: 60%;
  margin: 8% auto;
  color: #404040;
}

.showImg {
  padding: 0 80px 0 0;
}

.showh1 {
  padding: 0;
  margin: 0;
}

.showp1 {
  font-style: italic;
  font-size: 15px;
  padding: 0 0 30px 0;
  margin: 10px 0 0 0;
}

.showp2 {
  padding: 0 0 30px 0;
}

h4 {
  margin: 30px 0 0 0;
  padding: 0;
  font-size: 15px;
}

.showButton {
  background-color: #404040;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 15px;
}
.app{
  color: white;
  background-color: #404040;
  padding: 30px 0 30px 70px;
  text-align: left;
}
